import React, { useEffect } from "react";
import "./Navbar.css";
import Aos from "aos";
import "aos/dist/aos.css";
import { Link } from "react-router-dom";
import { Signinbutton } from "../signinbutton/signin";
const signout = () => {
  localStorage.removeItem("userinfo");
  window.location.reload();
};

export function Navbar() {
  const issignedin = localStorage.getItem("userinfo");
  let isadmin = false;
  if (issignedin) {
    const userinfo = JSON.parse(localStorage.getItem("userinfo"));
    if (userinfo.email === "tunghx0701@gmail.com") {
      isadmin = true;
    }
  }

  return (
    <nav className="navbar">
      <div className="cowandmilk">
        <span className="milk">ü•õ</span>
        <span className="cow">üêÑ</span>
      </div>
      <ul className="menu">
        <Link className="linkitem" to="/home">
          {" "}
          <li className="menuitem">Home</li>
        </Link>
        <Link className="linkitem" to="/films">
          <li className="menuitem">Films</li>
        </Link>
        <Link className="linkitem" to="/writings">
          {" "}
          <li className="menuitem">Writings</li>
        </Link>
        <Link className="linkitem" to="/books">
          <li className="menuitem">Books</li>
        </Link>
        <Link className="linkitem" to="/about">
          <li className="menuitem">About</li>
        </Link>

        {isadmin ? (
          <Link className="linkitem" to="/admin">
            <li className="menuitem">Admin</li>
          </Link>
        ) : null}
      </ul>

      <div className="signinbutton">
        {issignedin ? (
          <button className="signing signoutcolor" onClick={signout}>
            <span>Sign Out</span>
            <i class="fas fa-sign-in-alt"></i>
          </button>
        ) : (
          <Signinbutton />
        )}
      </div>
    </nav>
  );
}
