import './Admin.css'
import React, { useEffect, useState } from "react";
import Aos from "aos";
import { firebasefirestore } from "../firebase";
import "aos/dist/aos.css";
import { Texteditor } from "../components/texteditor/Texteditor";

import { Navbar } from "../components/navbar/Navbar";
export function Admin() {
  const [highlightpostcode, sethighlightpostcode] = useState();
  const changecode = (e) => {
    sethighlightpostcode(e.target.value);
  };

  const decodehighlights = (code) => {
    const highlightstrings = code.split("&&&");
    const results = [];
    for (let highlightstring of highlightstrings) {
      const highlightpostparts = highlightstring.split("---");
      results.push({
        postid: highlightpostparts[0],
        templateid: highlightpostparts[1],
        thumbnailimage: highlightpostparts[2],
      });
    }
    return results;
  };

  const savehighlights = async () => {
    const highlightpostinfo = decodehighlights(highlightpostcode);
    try {
      
      var postref = firebasefirestore.collection("setting").doc("highlightposts");
      await postref.update({
        posts: highlightpostinfo
      });
   
    } catch (err) {
      console.log(err);
    }
  };
  const encode =(data)=>{
    let result = "";
console.log(data)
for (let item of data){
    let itemstring = "";
    console.log(item)
    itemstring=`${item.postid}---${item.templateid}---${item.thumbnailimage}`;
    result+=itemstring;
    

result+="&&&";
}

console.log(result)
return(result.substring(0, result.length-3))

  }
  const gethighlightposts = async () => {
    const postref = await firebasefirestore
      .collection("setting")
      .doc("highlightposts")
      .get();
    if (postref.exists) {
      console.log(postref.data())
      sethighlightpostcode(encode(postref.data().posts));

    }   
  };
  useEffect(() => {
    gethighlightposts();
  }, []);

  return (
    <div className="admin">
      <Navbar />
      <Texteditor />
      <input className ="inputspace" value={highlightpostcode} onChange={changecode} />
      <button onClick={savehighlights}>Save Highlight Posts</button>
    </div>
  );
}
