import React, {useEffect, useState} from "react";
import "./Filmslide.css";
import {firebasefirestore} from '../../firebase'
import { Slideprops } from "../props/Slideprops";
import Slider from "react-slick";
import Aos from "aos";
import "aos/dist/aos.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
function NextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <img
      className={className}
      onClick={onClick}
      src="https://i.imgur.com/P1KTYoH.png"
    />
  );
}
function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <img
      className={className}
      onClick={onClick}
      src="https://i.imgur.com/WlIiNcU.png"
    />
  );
}

export function Filmslide() {
  
  const [highlightposts, sethighlightposts] = useState([
    { postid: "khFl5fTM66GfzyYF0nzl", templateid: 1, thumbnailimage: "https://i.imgur.com/rDcfBox.jpg" },
    { postid: "tRDwJrv65VjgV2M6UVKA", templateid: 2, thumbnailimage: "https://i.imgur.com/t5dFc3M.jpg" },
  ]);
  const gethighlightposts = async () => {
    const postref = await firebasefirestore
      .collection("setting")
      .doc("highlightposts")
      .get();
    if (postref.exists) {
      console.log(postref.data())
      sethighlightposts(postref.data().posts);

    }
  };

  useEffect(() => {
    gethighlightposts();
  }, []);

  const slickSetting = {
    arrow: true,
    slideToShow: 1,
    slideToScroll: 1,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };
  return (
    <Slider {...slickSetting}>
      
     {highlightposts.map((post)=>{
       return (
        <Slideprops
        {...post}
      />
       )
     })}

    </Slider>
  );
}
